import React, { Component } from 'react';
import PriceHistoryChart from './PriceHistoryChart';
// import OffersHistoryChart from './OffersHistoryChart';
import { OffersHistoryChart } from '../more_historical_data';
import { connect } from 'react-redux';
import { actions } from '../../../actions';
import { Col, Row, Container } from 'react-bootstrap';
import { List, ListItem, Checkbox, InputLabel, Icon } from '@material-ui/core';
import './assets/css/style.css';
import ApiFetcher from '../../../api_fetcher';
import CustomTooltip from '../../custom_tooltip';


const SERVICES = [
    {
        'range_key': 'amazon',
        'visible_key': 'amazon_visible',
        'reducer_function': 'set_amazon_range',
    },
    {
        'range_key': 'newone',
        'visible_key': 'new_visible',
        'reducer_function': 'set_new_range',
    },
    {
        'range_key': 'used_all',
        'visible_key': 'used_all_visible',
        'reducer_function': 'set_used_all_range',
    },
    {
        'range_key': 'used_like_new',
        'visible_key': 'used_like_new_visible',
        'reducer_function': 'set_used_like_new_range',
    },
    {
        'range_key': 'used_very_good',
        'visible_key': 'used_very_good_visible',
        'reducer_function': 'set_used_very_good_range',
    },
    {
        'range_key': 'used_good',
        'visible_key': 'used_good_visible',
        'reducer_function': 'set_used_good_range',
    },
    {
        'range_key': 'used_acceptable',
        'visible_key': 'used_acceptable_visible',
        'reducer_function': 'set_used_acceptable_range',
    },
    {
        'range_key': 'salesRank',
        'visible_key': 'salesRank_visible',
        'reducer_function': 'set_sales_rank_range',
    },
    {
        'range_key': 'warehouse',
        'visible_key': 'warehouse_visible',
        'reducer_function': 'set_warehouse_range',
    },
    {
        'range_key': null,
        'visible_key': 'salesRank_forecast_visible',
        'reducer_function': null,
    },
];

class PriceHistory extends Component {
    constructor() {
        super();

        this.onClickVisibility = this.onClickVisibility.bind(this);
        this.onChangePeriod = this.onChangePeriod.bind(this);
        this.rangeChanged = this.rangeChanged.bind(this);
        this.flipSalesRankReversed = this.flipSalesRankReversed.bind(this);
        
        this.priceHistoryChartCrosshairUpdated = this.priceHistoryChartCrosshairUpdated.bind(this);
        this.priceHistoryChartCrosshairHidden = this.priceHistoryChartCrosshairHidden.bind(this);
        this.priceHistoryChartToolTipUpdated = this.priceHistoryChartToolTipUpdated.bind(this);
        this.priceHistoryChartToolTipHidden = this.priceHistoryChartToolTipHidden.bind(this);

        this.offersHistoryChartCrosshairUpdated = this.offersHistoryChartCrosshairUpdated.bind(this);
        this.offersHistoryChartCrosshairHidden = this.offersHistoryChartCrosshairHidden.bind(this);
        this.offersHistoryChartToolTipUpdated = this.offersHistoryChartToolTipUpdated.bind(this);
        this.offersHistoryChartToolTipHidden = this.offersHistoryChartToolTipHidden.bind(this);    
        
        this.offersHistoryContainerRef = React.createRef();

        this.correctSizes = this.correctSizes.bind(this);
    }

    priceHistoryChartCrosshairUpdated(e) {
        if (this.offersHistoryChart) {
            this.offersHistoryChart.axisX[0].crosshair.showAt(e.value);
        }
    }

    priceHistoryChartCrosshairHidden(e) {
        if (this.offersHistoryChart) {
            this.offersHistoryChart.axisX[0].crosshair.hide();
        }
    }

    priceHistoryChartToolTipUpdated(e) {
        if (this.offersHistoryChart) {
            this.offersHistoryChart.toolTip.showAtX(e.entries[0].xValue);
        }
    }

    priceHistoryChartToolTipHidden(e) {
        if (this.offersHistoryChart) {
            this.offersHistoryChart.toolTip.hide();
        }
    }

    offersHistoryChartCrosshairUpdated(e) {
        if (this.priceHistoryChart) {
            this.priceHistoryChart.axisX[0].crosshair.showAt(e.value);
        }
    }

    offersHistoryChartCrosshairHidden(e) {
        if (this.priceHistoryChart) {
            this.priceHistoryChart.axisX[0].crosshair.hide();
        }
    }

    offersHistoryChartToolTipUpdated(e) {
        if (this.priceHistoryChart) {
            this.priceHistoryChart.toolTip.showAtX(e.entries[0].xValue);
        }
    }

    offersHistoryChartToolTipHidden(e) {
        if (this.priceHistoryChart) {
            this.priceHistoryChart.toolTip.hide();
        }
    }

    flipSalesRankReversed() {
        this.props.set_sales_rank_reversed();
    }

    rangeChanged(e) {
        const diff_days = (e.axisX[0].viewportMaximum - e.axisX[0].viewportMinimum) / (60 * 60 * 24 * 1000);

        const chart = e.chart;

        chart.data[0].dataPoints.forEach(item => {
            if (item.isGap) {
                item.y = null;
                delete item.toolTipContent;
            }
        });


        if (diff_days > 360 && diff_days <= 390) {
            chart.axisX[0].set("interval", 3);
            chart.axisX[0].set("intervalType", 'month');
        } else if (diff_days > 330 && diff_days <= 360) {
            chart.axisX[0].set("interval", 11);
            chart.axisX[0].set("intervalType", 'week');
        } else if (diff_days > 300 && diff_days <= 330) {
            chart.axisX[0].set("interval", 10);
            chart.axisX[0].set("intervalType", 'week');
        } else if (diff_days > 270 && diff_days <= 300) {
            chart.axisX[0].set("interval", 9);
            chart.axisX[0].set("intervalType", 'week');
        } else if (diff_days > 240 && diff_days <= 270) {
            chart.axisX[0].set("interval", 2);
            chart.axisX[0].set("intervalType", 'month');
        } else if (diff_days > 210 && diff_days <= 240) {
            chart.axisX[0].set("interval", 7);
            chart.axisX[0].set("intervalType", 'week');
        } else if (diff_days > 180 && diff_days <= 210) {
            chart.axisX[0].set("interval", 6);
            chart.axisX[0].set("intervalType", 'week');
        } else if (diff_days > 150 && diff_days <= 180) {
            chart.axisX[0].set("interval", 5);
            chart.axisX[0].set("intervalType", 'week');
        } else if (diff_days > 120 && diff_days <= 150) {
            chart.axisX[0].set("interval", 1);
            chart.axisX[0].set("intervalType", 'month');
        } else if (diff_days > 90 && diff_days <= 120) {
            chart.axisX[0].set("interval", 3);
            chart.axisX[0].set("intervalType", 'week');
        } else if (diff_days > 60 && diff_days <= 90) {
            chart.axisX[0].set("interval", 2);
            chart.axisX[0].set("intervalType", 'week');
        } else if (diff_days > 30 && diff_days <= 60) {
            chart.axisX[0].set("interval", 1);
            chart.axisX[0].set("intervalType", 'week');
        } else if (diff_days > 15 && diff_days <= 30) {
            chart.axisX[0].set("interval", 5);
            chart.axisX[0].set("intervalType", 'day');
        } else if (diff_days > 7 && diff_days <= 15) {
            chart.axisX[0].set("interval", 2);
            chart.axisX[0].set("intervalType", 'day');
        } else if (diff_days > 1 && diff_days <= 7) {
            chart.axisX[0].set("interval", Math.floor(diff_days * 2));
            chart.axisX[0].set("intervalType", 'hour');
        } else if (diff_days <= 1 && diff_days > 0) {
            chart.axisX[0].set("interval", 30);
            chart.axisX[0].set("intervalType", "minute");
        } else {
            this.forceUpdate();
        }


        if (chart && chart.container) {
            this.changeRangeOtherChart(chart.container.querySelector('canvas.canvasjs-chart-canvas').getAttribute('id'));
        }

        // console.log("type : " + e.type + ", trigger : " + e.trigger + ", AxisX viewportMininum : " + e.axisX[0].viewportMinimum + ", AxisX viewportMaximum : " + e.axisX[0].viewportMaximum);
    }

    changeRangeOtherChart(id) {
        let source = null;
        let destination = null;

        switch(id) {
            case 'canvasjs-offers-history':
                this.priceHistoryChart.options.axisX.viewportMinimum = this.offersHistoryChart.axisX[0].viewportMinimum;
                this.priceHistoryChart.options.axisX.viewportMaximum = this.offersHistoryChart.axisX[0].viewportMaximum;
                this.priceHistoryChart.render();
                break;
            case 'canvasjs-price-history':
                this.offersHistoryChart.options.axisX.viewportMinimum = this.priceHistoryChart.axisX[0].viewportMinimum;
                this.offersHistoryChart.options.axisX.viewportMaximum = this.priceHistoryChart.axisX[0].viewportMaximum;
                this.offersHistoryChart.render();
                break;
        }
    }

    changeColorOpacity(color, mouseLeave) {
        if (mouseLeave) {
            return `rgb(${color[0]}, ${color[1]}, ${color[2]})`;
        }

        return `rgba(${color[0]}, ${color[1]}, ${color[2]}, .2)`;
    }

    setLineThickness(lineThickness, index, mouseLeave = false) {
        if (this.priceHistoryChart) {
            if (!this.priceHistoryChart.options.data[index].visible && !mouseLeave) {
                return;
            }

            if (index > 2 && index < 7) {
                this.priceHistoryChart.options.data[index].markerSize = lineThickness;
            } else {
                this.priceHistoryChart.options.data[index].lineThickness = lineThickness;
            }

            this.priceHistoryChart.options.data.forEach((value, ind) => {
                if (ind === index) {
                    return;
                }

                value.color = this.changeColorOpacity(value.colorRGB, mouseLeave);
                if (value.lineColor) {
                    value.lineColor = this.changeColorOpacity(value.lineColorRGB, mouseLeave);
                }

                // if (mouseLeave) {
                //     value.visible = this.props.price_history[SERVICES[ind].visible_key];
                // } else {
                //     value.visible = false;
                // }

                // if current item is sales rank forecast then we have set it's opacity to .3 but not 1
                if (ind === 9 && mouseLeave) {
                    value.fillOpacity = .3;
                } else if (mouseLeave) {
                    value.fillOpacity = 1;
                } else {
                    value.fillOpacity = .2;
                }                
            });

            this.priceHistoryChart.render();
        }
    }

    onChangePeriod(e) {
        const charts = [
            this.priceHistoryChart,
            this.offersHistoryChart
        ];

        for(const chart of charts) {
            if (chart) {
                chart.axisX[0].set("interval", null);
                chart.axisX[0].set("intervalType", null);
                chart.axisX[0].set('minimum', null);
                chart.axisX[0].set('maximum', null);
                chart.axisX[0].set('viewportMinimum', null);
                chart.axisX[0].set('viewportMaximum', null);
            }
        }
        let inp = e.target;

        // console.log("XXX", inp.id, inp);
        // this.props.reset_price_history_zooming(true);
        
        this.priceHistoryChart.options.data[9].visible = false;
        this.props.change_period(inp.id);
        if (this.priceHistoryChart && this.priceHistoryChart.options) {
            this.priceHistoryChart.options.data[9].visible = this.props.price_history.salesRank_forecast_visible;
        }
    }

    loadData (id) {
        const cats = {}
        cats[actions.PRICE_HISTORY_USED_LIKE_NEW_VISIBLE] = {
            'range_key': 'used_like_new',
            'visible_key': 'used_like_new_visible',
            'reducer_function': 'set_used_like_new_range',
            'api_fetcher_key': 'ADDITIONAL_CATEGORY_USED_LIKE_NEW'
        };

        cats[actions.PRICE_HISTORY_USED_VERY_GOOD_VISIBLE] = {
            'range_key': 'used_very_good',
            'visible_key': 'used_very_good_visible',
            'reducer_function': 'set_used_very_good_range',
            'api_fetcher_key': 'ADDITIONAL_CATEGORY_USED_VERY_GOOD'
        };

        cats[actions.PRICE_HISTORY_USED_GOOD_VISIBLE] = {
            'range_key': 'used_good',
            'visible_key': 'used_good_visible',
            'reducer_function': 'set_used_good_range',
            'api_fetcher_key': 'ADDITIONAL_CATEGORY_USED_GOOD'
        };

        cats[actions.PRICE_HISTORY_USED_ACCEPTABLE_VISIBLE] = {
            'range_key': 'used_acceptable',
            'visible_key': 'used_acceptable_visible',
            'reducer_function': 'set_used_acceptable_range',
            'api_fetcher_key': 'ADDITIONAL_CATEGORY_USED_ACCEPTABLE'
        };

        cats[actions.PRICE_HISTORY_WAREHOUSE_VISIBLE] = {
            'range_key': 'warehouse',
            'visible_key': 'warehouse_visible',
            'reducer_function': 'set_warehouse_range',
            'api_fetcher_key': 'ADDITIONAL_CATEGORY_WAREHOUSE'
        };

        setTimeout(() => {
            const cat = cats[id];

            if (cat) {
                if (!this.props.price_history[cat.range_key].length && this.props.price_history[cat.visible_key]) {
                    ApiFetcher.getPriceHistoryAdditional(this.props.general.isbn, ApiFetcher[cat.api_fetcher_key]).then(data => {
                        const range = [];
                        const xValueFormatString = "DD MMM YYYY hh:mm TT";

                        if (!data.history.length) {
                            this.props.set_alert_open(true);
                            this.props.set_alert_title("Empty result");
                            this.props.set_alert_context_text("There is no relevant historical data");
                            this.props.show_price(id);
                        }

                        data.history.sort((item1, item2) => item1.when > item2.when ? 1 : (item1.when < item2.when ? -1 : 0));

                        data.history.forEach(value => {
                            const x = new Date()
                            x.setTime(value.when * 1000);

                            range.push({
                                xValueFormatString,
                                y: value.value[cat.range_key] || (range.length ? range[range.length - 1].y : 0),
                                x
                            });
                        });

                        this.props[cat.reducer_function](range);
                        this.props.is_set_tracking(data.tracking === true);
                    })
                        .catch(error => {
                            // this.props.set_alert_title("History Problem")
                            // this.props.set_alert_context_text(error.error_message);
                            // this.props.set_alert_open(true);
                        });
                }
            }
        }, 100);
    }

    correctSizes () {
        const priceLeftLabelWidth = this.priceHistoryChart.axisY[0].bounds.x2 - this.priceHistoryChart.axisY[0].bounds.x1;
        const priceChartWidth = this.priceHistoryChart.axisX[0].bounds.x2 - this.priceHistoryChart.axisX[0].bounds.x1;
        const offersLeftLabelWidth = this.offersHistoryChart.axisY[0].bounds.x2 - this.offersHistoryChart.axisY[0].bounds.x1;

        // console.log("FFFF", this.priceHistoryChart.data[0].dataPoints[0], this.priceHistoryChart.data[9].dataPoints[0]);

        var percent = 0;
        if (this.props.price_history.salesRank_forecast_visible && 
            this.priceHistoryChart.data[9].dataPoints.length) {
                const point = this.priceHistoryChart.axisX[0].convertValueToPixel(this.priceHistoryChart.data[9].dataPoints[0].x) - this.priceHistoryChart.axisY[0].bounds.x2 + 20;
                percent = 1 - point / priceChartWidth;
        }

        const width = priceChartWidth + priceLeftLabelWidth;

        this.offersHistoryContainerRef.current.style.width = `${width - (width * percent)}px`;
        this.offersHistoryContainerRef.current.style.marginLeft = `${priceLeftLabelWidth - offersLeftLabelWidth + 15}px`;

        this.offersHistoryChart.render();
    }

    onClickVisibility(e) {
        let inp = e.target;

        this.props.show_price(inp.id);

        this.loadData(inp.id);
    }

    componentDidUpdate () {
        if (this.priceHistoryChart && 
            (!this.priceHistoryChart.options.axisX.crosshair 
                || !this.priceHistoryChart.options.axisX.crosshair.updated)) {
            this.priceHistoryChart.options.axisX.crosshair.updated = this.priceHistoryChartCrosshairUpdated;
            this.priceHistoryChart.options.axisX.crosshair.hidden = this.priceHistoryChartCrosshairHidden;

            this.priceHistoryChart.options.toolTip.updated = this.priceHistoryChartToolTipUpdated;
            this.priceHistoryChart.options.toolTip.hidden = this.priceHistoryChartToolTipHidden;
        }

        if (this.offersHistoryChart && 
            (!this.offersHistoryChart.options.axisX.crosshair 
                || !this.offersHistoryChart.options.axisX.crosshair.updated)) {
            this.offersHistoryChart.options.axisX.crosshair.updated = this.offersHistoryChartCrosshairUpdated;
            this.offersHistoryChart.options.axisX.crosshair.hidden = this.offersHistoryChartCrosshairHidden;

            this.offersHistoryChart.options.toolTip.updated = this.offersHistoryChartToolTipUpdated;
            this.offersHistoryChart.options.toolTip.hidden = this.offersHistoryChartToolTipHidden;
        }

        if (this.priceHistoryChart && this.offersHistoryChart) {
            this.correctSizes();
        }
    }
    

    componentDidMount () {
        const PH = this.props.price_history;
        if (PH.used_like_new_visible && !PH.used_like_new.length) {
            this.loadData(actions.PRICE_HISTORY_USED_LIKE_NEW_VISIBLE);
        }

        if (PH.used_very_good_visible && !PH.used_very_good.length) {
            this.loadData(actions.PRICE_HISTORY_USED_VERY_GOOD_VISIBLE);
        }

        if (PH.used_good_visible && !PH.used_good.length) {
            this.loadData(actions.PRICE_HISTORY_USED_GOOD_VISIBLE);
        }

        if (PH.used_acceptable_visible && !PH.used_acceptable.length) {
            this.loadData(actions.PRICE_HISTORY_USED_ACCEPTABLE_VISIBLE);
        }

        if (PH.warehouse_visible && !PH.warehouse.length) {
            this.loadData(actions.PRICE_HISTORY_WAREHOUSE_VISIBLE);
        }

        window.onresize = this.correctSizes;
    }

    shouldComponentUpdate(nextProps, nextState) {
        // console.log("ZZZ", this.props.price_history.resetZooming, nextProps.price_history.resetZooming);
        // return this.props.price_history.resetZooming === nextProps.price_history.resetZooming;
        return true;
    }

    toggleLineThickness(lineType) {
        switch (lineType) {
            case 'amazon':

                break;
        }
    }

    render() {

        const PH = this.props.price_history;

        return (
            <Container id="price-history" fluid={true}>
                <Row>
                    <Col xs={10}>
                        <div id="price-history-container">
                            <PriceHistoryChart 
                                onRef={ref => this.priceHistoryChart = ref} 
                                rangeChanged={this.rangeChanged} 
                                /*key={new Date().getTime() * Math.random()}*/ 
                            />
                        </div>
                        {this.props.offers_history.offers_history_visible ? 
                            <div id="offers-history-container" ref={this.offersHistoryContainerRef}>
                                <OffersHistoryChart 
                                    onRef={ref => {
                                        this.offersHistoryChart = ref;
                                    }} 
                                    rangeChanged={this.rangeChanged} 
                                    key={new Date().getTime() * Math.random()} 
                                />
                            </div> 
                        : ""}
                    </Col>
                    <Col xs={2}>
                        <List>
                            <ListItem onMouseEnter={e => this.setLineThickness(5, 0)} onMouseLeave={e => this.setLineThickness(2, 0, true)} button={true} className="price-history-li price-history-li-amazon">
                                <Checkbox id={actions.PRICE_HISTORY_AMAZON_VISIBLE} onChange={this.onClickVisibility} checked={PH.amazon_visible} className="price-history-li price-history-li-amazon-cb" />
                                <InputLabel className="price-history-li-amazon-label" htmlFor={actions.PRICE_HISTORY_AMAZON_VISIBLE}>Amazon</InputLabel>
                            </ListItem>
                            <ListItem onMouseEnter={e => this.setLineThickness(3, 1)} onMouseLeave={e => this.setLineThickness(2, 1, true)} button={true} className="price-history-li price-history-li-new">
                                <Checkbox id={actions.PRICE_HISTORY_NEW_VISIBLE} onChange={this.onClickVisibility} checked={PH.new_visible} className="price-history-li price-history-li-new-cb" />
                                <InputLabel className="price-history-li-new-label" htmlFor={actions.PRICE_HISTORY_NEW_VISIBLE}>New</InputLabel>
                            </ListItem>
                            <ListItem onMouseEnter={e => this.setLineThickness(3, 2)} onMouseLeave={e => this.setLineThickness(1, 2, true)} button={true} className="price-history-li price-history-li-used-all">
                                <Checkbox className="price-history-li price-history-li-used-all-cb" id={actions.PRICE_HISTORY_USED_ALL_VISIBLE} onChange={this.onClickVisibility} checked={PH.used_all_visible} />
                                <InputLabel htmlFor={actions.PRICE_HISTORY_USED_ALL_VISIBLE} className="price-history-li-used-all-label">Used</InputLabel>
                            </ListItem>
                            <ListItem onMouseEnter={e => this.setLineThickness(10, 3)} onMouseLeave={e => this.setLineThickness(6, 3, true)} button={true} className="price-history-li price-history-li-used-like-new">
                                {this.props.price_history.used_like_new_visible && !this.props.price_history.used_like_new.length ? <Icon>hourglass_empty</Icon> : <Checkbox className="price-history-li price-history-li-used-like-new-cb" id={actions.PRICE_HISTORY_USED_LIKE_NEW_VISIBLE} onChange={this.onClickVisibility} checked={PH.used_like_new_visible} />}
                                <InputLabel htmlFor={actions.PRICE_HISTORY_USED_LIKE_NEW_VISIBLE} className="price-history-li-used-like-new-label">Used - Like New</InputLabel>
                            </ListItem>
                            <ListItem onMouseEnter={e => this.setLineThickness(10, 4)} onMouseLeave={e => this.setLineThickness(6, 4, true)} button={true} className="price-history-li price-history-li-used-very-good">
                                {this.props.price_history.used_very_good_visible && !this.props.price_history.used_very_good.length ? <Icon>hourglass_empty</Icon> : <Checkbox className="price-history-li price-history-li-used-very-good-cb" id={actions.PRICE_HISTORY_USED_VERY_GOOD_VISIBLE} onChange={this.onClickVisibility} checked={PH.used_very_good_visible} />}
                                <InputLabel htmlFor={actions.PRICE_HISTORY_USED_VERY_GOOD_VISIBLE} className="price-history-li-used-very-good-label">Used - Very Good</InputLabel>
                            </ListItem>
                            <ListItem onMouseEnter={e => this.setLineThickness(10, 5)} onMouseLeave={e => this.setLineThickness(6, 5, true)} button={true} className="price-history-li price-history-li-used-good">
                                {this.props.price_history.used_good_visible && !this.props.price_history.used_good.length ? <Icon>hourglass_empty</Icon> : <Checkbox className="price-history-li price-history-li-used-good-cb" id={actions.PRICE_HISTORY_USED_GOOD_VISIBLE} onChange={this.onClickVisibility} checked={PH.used_good_visible} />}
                                <InputLabel htmlFor={actions.PRICE_HISTORY_USED_GOOD_VISIBLE} className="price-history-li-used-good-label">Used - Good</InputLabel>
                            </ListItem>
                            <ListItem onMouseEnter={e => this.setLineThickness(10, 6)} onMouseLeave={e => this.setLineThickness(6, 6, true)} button={true} className="price-history-li price-history-li-used-acceptable">
                                {this.props.price_history.used_acceptable_visible && !this.props.price_history.used_acceptable.length ? <Icon>hourglass_empty</Icon> : <Checkbox className="price-history-li price-history-li-used-acceptable-cb" id={actions.PRICE_HISTORY_USED_ACCEPTABLE_VISIBLE} onChange={this.onClickVisibility} checked={PH.used_acceptable_visible} />}
                                <InputLabel htmlFor={actions.PRICE_HISTORY_USED_ACCEPTABLE_VISIBLE} className="price-history-li-used-acceptable-label">Used - Acceptable</InputLabel>
                            </ListItem>
                            <ListItem onMouseEnter={e => this.setLineThickness(3, 7)} onMouseLeave={e => this.setLineThickness(2, 7, true)} button={true} className="price-history-li price-history-li-sales-rank">
                                <Checkbox className="price-history-li price-history-li-sales-rank-cb" id={actions.PRICE_HISTORY_SALES_RANK_VISIBLE} onChange={this.onClickVisibility} checked={PH.salesRank_visible} />
                                <InputLabel className="price-history-li-sales-rank-label" htmlFor={actions.PRICE_HISTORY_SALES_RANK_VISIBLE}>
                                    Sales Rank
                                </InputLabel>
                                <Icon onClick={this.flipSalesRankReversed} style={{ fontSize: "1.2em", fontWeight: 'bold', cursor: "pointer", margin: "0 0 0px 10px" }}>{this.props.price_history.salesRank_reversed ? "arrow_downward_icon" : "arrow_upward_icon"}</Icon>
                            </ListItem>
                            <ListItem onMouseEnter={e => this.setLineThickness(10, 8)} onMouseLeave={e => this.setLineThickness(6, 8, true)} button={true} className="price-history-li price-history-li-warehouse">
                                {this.props.price_history.warehouse_visible && !this.props.price_history.warehouse.length ? <Icon>hourglass_empty</Icon> :
                                    <Checkbox className="price-history-li price-history-li-warehouse-cb" id={actions.PRICE_HISTORY_WAREHOUSE_VISIBLE} onChange={this.onClickVisibility} checked={PH.warehouse_visible} />}
                                <InputLabel className="price-history-li-warehouse-label" htmlFor={actions.PRICE_HISTORY_WAREHOUSE_VISIBLE}>
                                    Warehouse
                                </InputLabel>
                            </ListItem>
                            <hr />
                            <ListItem onMouseEnter={e => this.setLineThickness(2, 9)} onMouseLeave={e => this.setLineThickness(1, 9, true)} button={true} className="price-history-li price-history-li-sales-rank-forecast">
                                {this.props.price_history.salesRank_forecast_visible && !this.props.price_history.salesRank.length ? <Icon>hourglass_empty</Icon> :
                                    <Checkbox className="price-history-li price-history-li-sales-rank-forecast-cb" id={actions.PRICE_HISTORY_SALES_RANK_FORECAST_VISIBLE} onChange={this.onClickVisibility} checked={PH.salesRank_forecast_visible} />}
                                <InputLabel className="price-history-li-sales-rank-forecast-label" htmlFor={actions.PRICE_HISTORY_SALES_RANK_FORECAST_VISIBLE}>
                                    Sales Rank Forecast
                                    <CustomTooltip>This is a prediction of sales rank for 2 month.</CustomTooltip>
                                </InputLabel>
                            </ListItem>
                            <hr />
                            <ListItem onClick={this.onChangePeriod} button={true} className={`price-history-date-range-link ${PH.range === actions.PRICE_HISTORY_RANGE_DAY && 'active'}`} id={actions.PRICE_HISTORY_RANGE_DAY}>Day</ListItem>
                            <ListItem onClick={this.onChangePeriod} button={true} className={`price-history-date-range-link ${PH.range === actions.PRICE_HISTORY_RANGE_WEEK && 'active'}`} id={actions.PRICE_HISTORY_RANGE_WEEK}>Week</ListItem>
                            <ListItem onClick={this.onChangePeriod} button={true} className={`price-history-date-range-link ${PH.range === actions.PRICE_HISTORY_RANGE_MONTH && 'active'}`} id={actions.PRICE_HISTORY_RANGE_MONTH}>Month</ListItem>
                            <ListItem onClick={this.onChangePeriod} button={true} className={`price-history-date-range-link ${PH.range === actions.PRICE_HISTORY_RANGE_3_MONTH && 'active'}`} id={actions.PRICE_HISTORY_RANGE_3_MONTH}>3 Month</ListItem>
                            <ListItem onClick={this.onChangePeriod} button={true} className={`price-history-date-range-link ${PH.range === actions.PRICE_HISTORY_RANGE_YEAR && 'active'}`} id={actions.PRICE_HISTORY_RANGE_YEAR}>Year</ListItem>
                            <ListItem onClick={this.onChangePeriod} button={true} className={`price-history-date-range-link ${PH.range === actions.PRICE_HISTORY_RANGE_ALL && 'active'}`} id={actions.PRICE_HISTORY_RANGE_ALL}>All</ListItem>
                        </List>
                    </Col>
                </Row>
            </Container>
        )
    }
}

const mapStateToProps = state => ({
    price_history: state.price_history,
    offers_history: state.offers_history,
    progress_bar: state.progress_bar,
    general: state.general,
    track_product: state.track_product,
})

const mapDispatchToProps = dispatch => {
    return {
        show_price: action => dispatch({ type: action }),
        set_amazon_range: payload => dispatch({ type: actions.PRICE_HISTORY_AMAZON_RANGE, payload }),
        set_new_range: payload => dispatch({ type: actions.PRICE_HISTORY_NEW_RANGE, payload }),
        set_used_all_range: payload => dispatch({ type: actions.PRICE_HISTORY_USED_ALL_RANGE, payload }),
        set_used_like_new_range: payload => dispatch({ type: actions.PRICE_HISTORY_USED_LIKE_NEW_RANGE, payload }),
        set_used_very_good_range: payload => dispatch({ type: actions.PRICE_HISTORY_USED_VERY_GOOD_RANGE, payload }),
        set_used_good_range: payload => dispatch({ type: actions.PRICE_HISTORY_USED_GOOD_RANGE, payload }),
        set_used_acceptable_range: payload => dispatch({ type: actions.PRICE_HISTORY_USED_ACCEPTABLE_RANGE, payload }),
        set_sales_rank_range: payload => dispatch({ type: actions.PRICE_HISTORY_SALES_RANK_RANGE, payload }),
        set_warehouse_range: payload => dispatch({ type: actions.PRICE_HISTORY_WAREHOUSE_RANGE, payload }),
        set_used_offers: payload => dispatch({ type: actions.OFFERS_HISTORY_USED_RANGE, payload }),
        set_new_offers: payload => dispatch({ type: actions.OFFERS_HISTORY_NEW_RANGE, payload }),
        change_period: action => dispatch({ type: action }),
        change_progress_bar: payload => dispatch({ type: actions.PROGRESS_BAR_LOADING, payload }),
        set_alert_context_text: payload => dispatch({ type: actions.ALERT_CONTEXT_TEXT, payload }),
        set_alert_title: payload => dispatch({ type: actions.ALERT_TITLE, payload }),
        set_alert_open: payload => dispatch({ type: actions.ALERT_OPEN, payload }),
        set_sales_rank_reversed: () => dispatch({ type: actions.PRICE_HISTORY_SALES_RANK_REVERSED }),
        set_amazon_line_thickness: payload => dispatch({ type: actions.PRICE_HISTORY_AMAZON_LINE_THICKNESS, payload }),
        set_new_line_thickness: payload => dispatch({ type: actions.PRICE_HISTORY_NEW_LINE_THICKNESS, payload }),
        set_used_line_thickness: payload => dispatch({ type: actions.PRICE_HISTORY_USED_LINE_THICKNESS, payload }),
        set_used_like_new_line_thickness: payload => dispatch({ type: actions.PRICE_HISTORY_USED_LIKE_NEW_LINE_THICKNESS, payload }),
        set_used_very_good_line_thickness: payload => dispatch({ type: actions.PRICE_HISTORY_USED_VERY_GOOD_LINE_THICKNESS, payload }),
        set_used_good_line_thickness: payload => dispatch({ type: actions.PRICE_HISTORY_USED_GOOD_LINE_THICKNESS, payload }),
        set_used_acceptable_line_thickness: payload => dispatch({ type: actions.PRICE_HISTORY_USED_ACCEPTABLE_LINE_THICKNESS, payload }),
        set_sales_rank_line_thickness: payload => dispatch({ type: actions.PRICE_HISTORY_SALES_RANK_LINE_THICKNESS, payload }),
        is_set_tracking: payload => dispatch({ type: actions.TRACK_PRODUCT_IS_SET, payload }),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(PriceHistory);