import React, {Component} from 'react';
import {connect} from 'react-redux';
import {actions} from '../../../actions';
import data from './settings_offers_history_chart.json';
import ApiFetcher from '../../../api_fetcher';
import CanvasJSReact from '../../canvasjs3.2.6/canvasjs.react';

var CanvasJSChart = CanvasJSReact.CanvasJSChart;

class OffersHistoryChart extends Component {
    constructor () {
        super()
        this.rangeChanged = this.rangeChanged.bind(this);
    }

    rangeChanged(e) {
        // callback came from property
        this.props.rangeChanged(e);
    }

    getAppropriateRange (items) {
        if (!items.length) {
            return items;
        }

        // let all = this.props.price_history.chart.data[0].dataPoints.map(item => {
        //     return {
        //         x: item.x,
        //         y: 0
        //     }
        // }).concat(items);

        // if (this.props.price_history.chart.data[9].dataPoints 
        //     && this.props.price_history.chart.data[9].dataPoints.length && 
        //     this.props.price_history.salesRank_forecast_visible) {
        //         all = all.concat(this.props.price_history.chart.data[9].dataPoints);
        //     }

        const fillHistory = () => {
            let allTimes = [];

            const maximum = new Date();

            const range = {}
            range[actions.PRICE_HISTORY_RANGE_DAY] = new Date(maximum);
            range[actions.PRICE_HISTORY_RANGE_WEEK] = new Date(maximum);
            range[actions.PRICE_HISTORY_RANGE_MONTH] = new Date(maximum);
            range[actions.PRICE_HISTORY_RANGE_3_MONTH] = new Date(maximum);
            range[actions.PRICE_HISTORY_RANGE_YEAR] = new Date(maximum);
            range[actions.PRICE_HISTORY_RANGE_ALL] = new Date(this.props.price_history.chart.axisX[0].minimum);

            range[actions.PRICE_HISTORY_RANGE_DAY].setDate(maximum.getDate() - 1);
            range[actions.PRICE_HISTORY_RANGE_WEEK].setDate(maximum.getDate() - 7);
            range[actions.PRICE_HISTORY_RANGE_MONTH].setMonth(maximum.getMonth() - 1);
            range[actions.PRICE_HISTORY_RANGE_3_MONTH].setMonth(maximum.getMonth() - 3);
            range[actions.PRICE_HISTORY_RANGE_YEAR].setFullYear(maximum.getFullYear() - 1);

            const minimum = range[this.props.price_history.range || actions.PRICE_HISTORY_RANGE_ALL];

            if (minimum == 'Invalid Date' || maximum == 'Invalid Date') {
                return items;
            }

            for (let i = minimum.getTime(); i < maximum.getTime(); i += 3600000) {
                    allTimes.push(i);
            }

            items.forEach(item => {
                const time = item.x.getTime();
                if (allTimes.indexOf(time) === -1) {
                    allTimes.push(time);
                }
            });

            // allTimes.sort((a, b) => b - a);


            items.forEach(item => {
                const time = item.x.getTime();
                const index = allTimes.indexOf(time);
                if (index !== -1) {
                    allTimes[index] = item;
                }
            });

            let all = allTimes.map(item => {
                if (typeof item === 'number') {
                    return {
                        x: new Date(item),
                        y: 0,
                        xValueFormatString: "DD MMM YYYY hh:mm TT"
                    }
                } else {
                    return item;
                }
            });

            return all;
        }

        if (!this.props.price_history.chart) {
            return items;
        }

        const filtered_items = [];        
        const min = new Date();

        switch (this.props.price_history.range) {
            case actions.PRICE_HISTORY_RANGE_DAY:
                min.setTime(min.getTime() - 60 * 60 * 24 * 1000);
                break;
            case actions.PRICE_HISTORY_RANGE_WEEK:
                min.setTime(min.getTime() - 60 * 60 * 24 * 8 * 1000);
                break;
            case actions.PRICE_HISTORY_RANGE_MONTH:
                min.setTime(min.getTime() - 60 * 60 * 24 * 32 * 1000);
                break;
            case actions.PRICE_HISTORY_RANGE_3_MONTH:
                min.setTime(min.getTime() - 60 * 60 * 24 * 31 * 3 * 1000);
                break;
            case actions.PRICE_HISTORY_RANGE_YEAR:
                min.setTime(min.getTime() - 60 * 60 * 24 * 366 * 1000);
                break;
            case actions.PRICE_HISTORY_RANGE_ALL:
                return items;
        }

        let all = items;

        for (let i = all.length - 1; i >= 0; i--) {
            if (all[i].x >= min) {
                let item = all[i];
                filtered_items.push(item);
            } else {
                break;
            }
        }

        return filtered_items;
    }

    resizeChartWidthConsistentToPriceChart () {
        if (this.props.price_history && this.props.price_history.chart) {
            const bounds = this.props.price_history.chart.axisX[0].get('bounds');
            if (this.chart) {
                this.chart.set('width', bounds.width + 70);
            }
        }
    }

    componentDidMount () {

        if (!this.props.offers_history.chart && this.chart) {
            this.props.set_chart(this.chart);
        }

        if (this.chart) {
            this.chart.canvas.setAttribute('id', 'canvasjs-offers-history');
        }
    }

    componentWillMount () {

        if (!this.props.offers_history.used_offers.length && !this.props.offers_history.new_offers.length) {
            const used_offers = [], new_offers = [];
            const xValueFormatString = "DD MMM YYYY hh:mm TT";
            ApiFetcher.getPriceHistoryAdditional(this.props.general.isbn, ApiFetcher.ADDITIONAL_CATEGORY_HISTORICAL_DATA).then(data => {
                data.history.sort((item1, item2) => item1.when > item2.when ? 1 : (item1.when < item2.when ? -1 : 0));
                
                data.history.forEach(value => {
                    const x = new Date();
                    x.setTime(value.when * 1000);

                    new_offers.push({
                        xValueFormatString,
                        y: value.value.historical_data[0] && value.value.historical_data[0] !== -1 ? value.value.historical_data[0] : (new_offers.length ? new_offers[new_offers.length - 1].y : 0),
                        x
                    });

                    used_offers.push({
                        xValueFormatString,
                        y: value.value.historical_data[1] && value.value.historical_data[1] !== -1 ? value.value.historical_data[1] : (used_offers.length ? used_offers[used_offers.length - 1].y : 0),
                        x
                    });
                });



                this.props.set_new_offers(new_offers);
                this.props.set_used_offers(used_offers);

            }).catch(error => {
                // this.props.set_alert_title("More historical data problem")
                // this.props.set_alert_context_text(error.error_message);
                // this.props.set_alert_open(true);
            });
        }
    }

    render () {
        const PH = this.props.offers_history;

        data.data[0].dataPoints = this.getAppropriateRange(PH.used_offers || [], 'Used Offer Count');
        data.data[1].dataPoints = this.getAppropriateRange(PH.new_offers || [], 'New Offer Count');

        switch(this.props.price_history.range) {
            case actions.PRICE_HISTORY_RANGE_DAY:
                data.axisX.interval = 1;
                data.axisX.intervalType = 'hour';
                break;
            case actions.PRICE_HISTORY_RANGE_WEEK:
                data.axisX.interval = 1;
                data.axisX.intervalType = 'day';
                break;
            case actions.PRICE_HISTORY_RANGE_MONTH:
                data.axisX.interval = 2;
                data.axisX.intervalType = 'day';
                break;
            case actions.PRICE_HISTORY_RANGE_3_MONTH:
                data.axisX.interval = 1;
                data.axisX.intervalType = 'week';
                break;
            case actions.PRICE_HISTORY_RANGE_YEAR:
                data.axisX.interval = 1;
                data.axisX.intervalType = 'month';
                break;
            case actions.PRICE_HISTORY_RANGE_ALL:
                data.axisX.interval = 5;
                data.axisX.intervalType = 'month';
                break;
        }

        data.rangeChanged = this.rangeChanged;
        data.height = 250;
        
        if (data.data[0].dataPoints.length && data.data[1].dataPoints.length){
            return (<CanvasJSChart key={new Date().getTime() * Math.random()} onRef={ref => {
                this.chart = ref;
                if (this.props.onRef) {
                    this.props.onRef(ref);
                }
            }} options={data} />);
        } else {
            return "";
        }
    }
}

const mapStateToProps = state => ({
    price_history: state.price_history,
    offers_history: state.offers_history,
    general: state.general
})

const mapDispatchToProps = dispatch => {
    return {
        set_used_offers: payload => dispatch({type: actions.OFFERS_HISTORY_USED_RANGE, payload}),
        set_new_offers: payload => dispatch({type: actions.OFFERS_HISTORY_NEW_RANGE, payload}),
        set_alert_context_text: payload => dispatch({type: actions.ALERT_CONTEXT_TEXT, payload}),
        set_alert_open: payload => dispatch({type: actions.ALERT_OPEN, payload}),
        set_alert_title: payload => dispatch({type: actions.ALERT_TITLE, payload}),
        set_chart: payload => dispatch({type: actions.OFFERS_HISTORY_CHART, payload}),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(OffersHistoryChart);